<template>
  <div class="card_detail">
      <input type="button" @click="goback()" value="返回列表" class="btn btn-warning">
      <span v-if="this.status == 'loading'">正在加载数据...</span>
      <div v-else>
          <h2 style="color: red">
              <img v-bind:src="this.host + this.card.picture.http_url" style="width: 15%; height: 15%">
              {{this.card.name}}({{this.card.raw_name}})
          </h2>
          
          <table class="table table-condensed table-hover ">
              <tbody>
                  <tr>
                    <th width="12%">卡号：</th>
                    <td width="12%">{{this.card.card_code}}</td>
                    <th width="12%">工作号：</th>
                    <td width="12%">{{this.card.work_code}}</td>
                    <th width="12%">类别：</th>
                    <td width="12%">{{this.card.product.name}}({{this.card.product.raw_name}})</td>
                    <th width="12%">新类别：</th>
                    <td width="12%">{{this.card.new_product.name}}({{this.card.new_product.raw_name}})</td>
                </tr>
                <tr>
                    <th>颜色：</th>
                    <td v-bind:style="{backgroundColor: this.card.color}"></td>
                    <th>WingType：</th>
                    <td>{{this.card.wing_type}}</td>
                    <th>卡片类型：</th>
                    <td>{{this.card.card_type}}</td>
                    <th>原始链接：</th>
                    <td><a v-bind:href="this.card.raw_url" target="_blank">点击查看</a></td>
                </tr>
                <tr>
                    <th>等级：</th>
                    <td>{{this.card.level == -1 ? '-' : this.card.level}}</td>
                    <th>花费：</th>
                    <td>{{this.card.cost}}</td>
                    <th>灵魂值：</th>
                    <td>{{this.card.soul}}</td>
                    <th>攻击力：</th>
                    <td>{{this.card.attack_value == -1 ? '-' : this.card.attack_value}}</td>
                </tr>
                <tr>
                    <th>触发器：</th>
                    <td colspan="3">{{this.card.triggers.map((e) => {return e.raw_name}).join()}}</td>
                    <th>特征</th>
                    <td colspan="3">{{this.card.feature}}</td>
                </tr>
                <tr>
                    <th>原始效果：</th>
                    <td colspan="7" v-html="this.card.raw_content"></td>
                </tr>
                <tr>
                    <th>翻译效果：</th>
                    <template v-if="this.editing">
                        <td colspan="6">
                            <textarea v-model="card.content" style="width: 100%; height: 100px"></textarea>
                            <td>
                                <input @click="save_content()" type="button" value="保存" />
                                <input @click="edit_content()" type="button" value="取消" />
                            </td>
                        </td>
                    </template>    
                    <template v-else>
                        <td colspan="6" v-html="this.card.content"></td>
                        <td><input @click="edit_content()" type="button" value="修改" /></td>
                    </template>
                </tr>
              </tbody>
          </table>
      </div>
  </div>
</template>


<script>
    export default {
        name: 'CardDetail',
        data() {
            return {
                status: 'loading',
                card: {content: ""},
                host: "http://xiayuanyin.cn:4002",
                // host: "http://localhost:3000",
                editing: false
            }
        },

        methods: {
            goback() {
                this.$router.go(-1)
            },

            edit_content() {
                this.editing = !this.editing
            },

            save_content() {
                let url = `${this.host}/api/v1/cards/${this.$route.params.id}`
                fetch(url, {
                    method: "PUT",
                    headers: { 'Accept': 'application/json', 'Content-Type': 'application/json'},
                    body: JSON.stringify({content: this.card.content})
                })
                .then((responseData) => {return responseData.json()})
                .then((data) => {
                    this.editing = false
                })
                .catch((err) => {
                    alert('更新失败')
                })
            },

            get_card() {
                let url = `${this.host}/api/v1/cards/${this.$route.params.id}`
                fetch(url, {
                    method: "GET",
                    headers: {'Accept': 'application/json'}
                })
                .then((responseData) => {return responseData.json()})
                .then((data) => {
					console.log(data)
					this.card = data.card
					this.status = 'loaded'
                })
            }
        },

        created() {
            this.get_card()
        }
    }
</script>

<style scoped>
    .table th {
        background-color: lightgray
    }
</style>



